import React, { Component } from "react";
import less from "./user.css";
import { withRouter } from "next/router";
import gqlClient from "GQL/GraphQL";
import { QUERY_USER_BY_ID } from "GQL/Query";

class User extends Component {
  constructor (props) {
    super(props);
    this.state = {
      userState: {}
    };
  }

  componentDidMount () {
    const id = this.props.router.query.id;
    gqlClient.query({
      query: QUERY_USER_BY_ID(),
      variables: { id }
    }).then(({ data: { user } }) => user)
      .then(user => {
        this.setState({ userState: user })
      })
  }

  render () {
    const user = this.state.userState;
    console.log(user);

    return (
      <div className={ less.user }>
        <div className={less["content-profile-page"]}>
          <div className={less["profile-user-page card"]}>
            <div className={less["img-user-profile"]}>
              <img className={less["profile-bgHome"]}
                   src="https://37.media.tumblr.com/88cbce9265c55a70a753beb0d6ecc2cd/tumblr_n8gxzn78qH1st5lhmo1_1280.jpg"/>
              <img className={less["avatar"]} src="http://gravatar.com/avatar/288ce55a011c709f4e17aef7e3c86c64?s=200"
                   alt="jofpin"/>
            </div>
            <button>Follow</button>
            <div className={less["user-profile-data"]}>
              <h1>Jose Pino</h1>
              <p>github.com/jofpin</p>
            </div>
            <div className={less["description-profile"]}>Front-end | Security Researcher | CSS Warrior | <a
              href="https://twitter.com/bullgit" title="bullgit"><strong>@bullgit</strong></a> | I love to create small
              things for the internet!
            </div>
            <ul className={less["data-user"]}>
              <li><a><strong>3390</strong><span>Posts</span></a></li>
              <li><a><strong>718</strong><span>Followers</span></a></li>
              <li><a><strong>239</strong><span>Following</span></a></li>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(User);



